<template>
  <div class="home-section">
    <div class="home-content-section">
      <div class="home-title">4.23世界读书日</div>
      <el-card class="home-card" :body-style="{ padding: '0px' }">
        <!--<img src="../../public/99.jpg" id="img" class="book-img">-->
        <el-upload
                class="avatar-uploader"
                :show-file-list="false"
                action=""
                :before-upload="beforeAvatarUpload">
          <img v-if="imageUrl" :src="imageUrl" class="book-img" id="img">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
        <div v-if="showPass" class="pass-section">
          <div>识别成功，你的口令是：</div>
          <div>
            <span class="pass-item">我</span>
            <span class="pass-item">爱</span>
            <span class="pass-item">读</span>
            <span class="pass-item">书</span>
          </div>
        </div>
        <div v-if="showPass" class="rule-section">
          <img src="../assets/imgcode.png"/>
          <div>扫码关注公众号，回复上方口令即可获得抽奖链接</div>
        </div>
        <div style="padding: 14px;">
          <div>
            <el-button type="text" class="card-btn" @click="getTF">{{btnText}}</el-button>
          </div>
        </div>
      </el-card>
    </div>
    <img class="bg-img" src="../assets/background.png"/>
  </div>
</template>

<script>
// @ is an alias to /src
// import * as cocoSsd from '@tensorflow-models/coco-ssd'
import * as tf from '@tensorflow/tfjs';
import {loadGraphModel} from '@tensorflow/tfjs-converter';
export default {
  name: 'Home',
  data: () => ({
    imageUrl:"",
    btnText:"加载模型中...",
    model:null,
    showPass:false
  }),
  created(){
    this.initModel();
  },
  methods:{
    initModel:async function(){
      const MODEL_URL = process.env.BASE_URL+"web_model/model.json";
      this.model = await loadGraphModel(MODEL_URL);
      console.log(this.model);
      this.btnText = "识别图片";
    },
    getTF:async function(){
      switch (this.btnText) {
        case '加载模型中...':
          this.$message({
            message: '模型正在加载中,请耐心等待',
            type: 'warning'
          });
          break;
        case '识别图片':
          if (!this.imageUrl){
            this.$message({
              message: '请先选择图片',
              type: 'warning'
            });
          }else{
            this.startRecognition();
          }
          break;

      }

    },
    buildDetectedObjects:function(scores, threshold, imageWidth, imageHeight, boxes, classes, classesDir) {
          const detectionObjects = [];

          scores.forEach((score, i) => {
              if (score > threshold) {
                  const bbox = [];
                  const minY = boxes[i * 4] * imageHeight;
                  const minX = boxes[i * 4 + 1] * imageWidth;
                  const maxY = boxes[i * 4 + 2] * imageHeight;
                  const maxX = boxes[i * 4 + 3] * imageWidth;
                  bbox[0] = minX;
                  bbox[1] = minY;
                  bbox[2] = maxX - minX;
                  bbox[3] = maxY - minY;
                  console.log('1111')
                  detectionObjects.push({
                      class: classes[i],
                      label: classesDir[classes[i]].name,
                      score: score.toFixed(4),
                      bbox: bbox
                  });
              }
          });

          return detectionObjects
    },
    beforeAvatarUpload(file) {
      this.imageUrl = window.URL.createObjectURL(file)
    },
    startRecognition:async function(){
      try{
        this.btnText="识别中..."
        const img = document.getElementById('img');
        let modelres =await this.model.executeAsync(tf.browser.fromPixels(img).expandDims(0));
        let classesDir = {
          1: {
            name: 'book',
            id: 1,
          }
        };
        let res=this.buildDetectedObjects(modelres[0].dataSync(),0.20,img.width,img.height,modelres[3].dataSync(),
                modelres[1].dataSync(),classesDir);
        console.log((res))
        if (res.length>=0){
          this.showPass=true;
        } else {
          this.showPass=false;
        }
        let canvas = document.createElement("canvas");
        canvas.width = img.width;
        canvas.height = img.height;
        let ctx = canvas.getContext("2d")
        ctx.drawImage(img,0,0,img.width,img.height);
        ctx.strokeStyle="#FF0000";
        ctx.strokeRect(res[0].bbox[0],res[0].bbox[1],res[0].bbox[2],res[0].bbox[3]);
        canvas.toBlob((blob)=>{
          this.imageUrl=URL.createObjectURL(blob)
        });
        this.btnText="识别图片"

      }catch (e) {
        console.log(e)
        this.btnText="识别图片"
      }
    }
  }

}
</script>

